.btn-demo {
	display: inline-block;
	min-width: 139px;
	padding: 8px 30px;
	font-family: $font-sec;
	font-size: 11px;
	letter-spacing: .08em;
	font-weight: 500;
	text-transform: uppercase;
	border-radius: 20px;
	
	&-default {
		position: relative;
		z-index: 1;
		color: $white;
		
		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			border: 2px solid #0971f2;
			border-radius: 20px;
			opacity: 0;
			transition: .3s ease-out all;
		}
		
		&:after {
			content: '';
			z-index: -1;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: -moz-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			background: -webkit-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			background: -ms-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			border-radius: 20px;
			transition: .3s ease-out all;
		}
		
		&, &:focus, &:active {
		  color: $white;
		}
		
		&:hover {
			color: $primary;
			&:before {
				opacity: 1;
			}
			&:after {
				opacity: 0;
			}
		}
	}
	
	&-primary {
		position: relative;
		z-index: 1;
		color: $white;
		 &:focus, &:active {
			color: $white;
			 &:hover {
				 color: $primary;
			 }
		}
		
		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			border: 2px solid #0971f2;
			border-radius: 20px;
			opacity: 0;
			transition: .3s ease-out all;
		}
		
		&:after {
			content: '';
			z-index: -1;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: -moz-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			background: -webkit-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			background: -ms-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			border-radius: 20px;
			transition: .3s ease-out all;
		}
		
		&:hover {
			
			&:before {
				opacity: 1;
			}
			&:after {
				opacity: 0;
			}
		}
	}
	
	&-primary-outline.btn-demo {
		color: $white;
		&:focus, &:active {
			color: $white;
		}
		
		&:after {
			opacity: 0;
		}
		&:before {
			opacity: 1;
		}
		
		&:hover {
			&:after {
				opacity: 1;
			}
			&:before {
				opacity: 0;
			}
		}
	}
}

//typed text
//---------------
.typed-text {
	color: #0971f2;
	display: inline-block;
	color: #0971f2;
	background: -moz-linear-gradient(90deg, #0971f2 0%, #cd63dc 100%);
	background: -webkit-linear-gradient(90deg, #0971f2 0%, #cd63dc 100%);
	background: -o-linear-gradient(90deg, #0971f2 0%, #cd63dc 100%);
	background: -ms-linear-gradient(90deg, #0971f2 0%, #cd63dc 100%);
	background: linear-gradient(90deg, #0971f2 0%, #cd63dc 100%);
	background-size: 100% 100%;
	-webkit-background-clip: text;
	-webkit-text-fill-color: rgba(0, 0, 0, 0);
}

.typed-cursor {
	vertical-align: text-top;
	color: #0971f2;
	opacity: 1;
	-webkit-animation: blink 0.7s infinite;
	-moz-animation: blink 0.7s infinite;
	animation: blink 0.7s infinite;
}

@keyframes blink {
	0% { opacity: 1; }
	50% { opacity: 0; }
	100% { opacity: 1; }
}

@-webkit-keyframes blink {
	0% { opacity: 1; }
	50% { opacity: 0; }
	100% { opacity: 1; }
}

@-moz-keyframes blink {
	0% { opacity: 1; }
	50% { opacity: 0; }
	100% { opacity: 1; }
}

// Portfolio
.portfolio-filter.portfolio-filter-tab-demo {
	text-align: center;
	margin-right: -5px;
	margin-bottom: -5px;
	> li {
		display: inline-block;
		float: none;
		margin-right: 5px;
		margin-bottom: 5px;
	}
	a {
		position: relative;
		z-index: 1;
		display: inline-block;
		font-family: $font-sec;
		font-weight: 600;
		letter-spacing: .04em;
		font-size: 16px;
		padding: 9px 15px;
		background-color: transparent !important;
		border: none !important;
		color: #151515;
		
		@media (min-width: 992px) {
			padding: 9px 31px;
		}
		
		&:before {
			content: '';
			z-index: -1;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: -moz-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			background: -webkit-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			background: -ms-linear-gradient(0deg, rgb(56, 86, 191) 0%, rgb(37, 135, 237) 100%);
			border-radius: 20px;
			opacity: 0;
			transition: .3s ease-out all;
		}
		
		&:hover {
			color: $white;
			&:before {
				opacity: 1;
			}
		}
	}
	
	li.active {
		a {
			color: $white;
		}
	}
	li.active a:before {
		opacity: 1;
	}
}

